.container {
  max-width: 400px;
  margin: 0 auto;
}

.form {
  display: grid;
  font-size: 14px;
  margin: 20px;
}

.span {
  font-size: 12px;
  margin: 2px 0;
  font-style: italic;
}

.input {
  display: block;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
}

.button {
  padding: 10px;
  border-radius: 5px;
  background: transparent;
  transition: 0.3s;
}

.button:hover {
  cursor: pointer;
  background: rgb(219, 219, 219);
}